<template>
  <v-app id="inspire">
    <v-container fluid>
      <v-row style="height: fit-content">
        <v-col>
          <h3
            class="white--text"
            style="font-family: 'Caveat', cursive; font-size: 20px"
          >
            Follow Me In
          </h3>
          <ButtonsSocial />

          <v-app-bar color="rgba(0,0,0,0)" flat class="mt-4">
            <div class="v-toolbar__content">
              <v-col cols="12" sm="6">
                <search
                  toolbarColor="#eef3f7"
                  toolbar-title=""
                  inputColor="green"
                  itemColor="orange lighten-0"
                  :light="true"
                  :data="tracks"
                  :states="states"
                  @selectionAdded="GetSearchIndex"
                  @reset="resetSelect"
                />
              </v-col>
            </div>
          </v-app-bar>
        </v-col>
      </v-row>

      <v-progress-circular
        v-if="!info"
        :size="200"
        :width="10"
        color="red "
        class="mt-10"
        indeterminate
      ></v-progress-circular>

      <v-row v-if="info">
        <v-col :sm="12" :lg="6" class="order-10 order-lg-0">
          <carousel-3d
            :controls-visible="false"
            :clickable="true"
            :height="300"
            ref="mycarousel"
            @after-slide-change="onAfterSlideChange"
          >
            <slide :index="i" :key="i" v-for="(track, i) in tracks">
              <figure>
                <img :src="track.cover" />
                <figcaption>
                  <v-btn text color="white">{{ track.artist }}</v-btn>
                </figcaption>
              </figure>
            </slide>
          </carousel-3d>
          <v-card
            :loading="loading"
            class="mx-2 my-12 pa-2"
            max-width="100%"
            outlined
            :dark="!switch1"
            :color="switch1 ? '#fff' : ''"
          >
            <template slot="progress">
              <v-progress-linear
                color="deep-purple"
                height="10"
                indeterminate
              ></v-progress-linear>
            </template>
            <v-card-title>Music Info</v-card-title>
            <v-card-text>
              <v-row align="center" class="mx-0">
                <v-rating
                  v-model="rating"
                  empty-icon="$ratingFull"
                  hover
                  color="amber"
                  background-color="grey darken-1"
                  dense
                  half-increments
                  size="14"
                ></v-rating>
                <div class="grey--text ms-5">
                  {{ rating }} (listener rating)
                </div>
              </v-row>
              <v-row>
                <v-card-title
                  class="pa-1 ma-0"
                  :class="{ 'black--text': switch1 }"
                  :style="{
                    color: switch1 ? '#f1f1f1' : color.color5,
                  }"
                  >Name :
                </v-card-title>
                <v-card-title class="text-subtitle-3 pa-1 ma-0">
                  {{ info.title }}
                </v-card-title>
              </v-row>
              <v-row>
                <v-card-title
                  class="pa-1 ma-0"
                  :class="{ 'black--text': switch1 }"
                  :style="{
                    color: switch1 ? '#f1f1f1' : color.color5,
                  }"
                  >Released :
                </v-card-title>
                <v-card-title class="text-subtitle-2 pa-1 ma-0">
                  {{ info.Released }}
                </v-card-title>
              </v-row>
              <v-row>
                <v-card-title
                  class="pa-1 ma-0"
                  :class="{ 'black--text': switch1 }"
                  :style="{
                    color: switch1 ? '#f1f1f1' : color.color5,
                  }"
                  >Label :
                </v-card-title>
                <v-card-title class="text-subtitle-3 pa-1 ma-0">
                  {{ info.Label }}
                </v-card-title>
              </v-row>

              <!-- <v-divider class="ma-4"></v-divider> -->
              <v-row>
                <v-card-title
                  class="pa-1 ma-0 d-inline"
                  :class="{ 'black--text': switch1 }"
                  :style="{
                    color: switch1 ? '#f1f1f1' : color.color5,
                  }"
                >
                  Description :
                </v-card-title>
                <v-card-title
                  class="text-subtitle-3 pa-3 ma-0 text-left text-break"
                  v-html="info.Description"
                >
                </v-card-title>
              </v-row>
              <v-row>
                <v-sheet style="width: 100%">
                  <v-card-title
                    class="pa-1 mb-5"
                    :class="{ 'black--text': switch1 }"
                  >
                    comments :
                  </v-card-title>

                  <v-card
                    width="100%"
                    height="fit-content"
                    class="overflow-auto"
                    v-if="comments.length == 0"
                  >
                    <v-card-text>
                      <h3 class="font-weight-black font-italic">
                        Be The First One To Comment
                      </h3>
                    </v-card-text>
                  </v-card>

                  <v-row :key="p" v-for="(comment, p) in comments">
                    <v-card class="ma-0 pa-0" style="width: 100%">
                      <v-list-item>
                        <v-list-item-content>
                          <v-list-item-title
                            class="
                              text-h6
                              font-italic
                              pa-0
                              ma-0
                              text-left text-capitalize
                            "
                            :class="{ 'black--text': switch1 }"
                            v-html="comment.name"
                          >
                          </v-list-item-title>
                          <v-list-item-subtitle
                            class="
                              text-subtitle-3
                              pa-3
                              ma-0
                              text-left
                              commentContent
                            "
                            :style="{
                              backgroundColor: switch1
                                ? '#e1e1e1'
                                : color.color2,
                            }"
                            v-html="comment.message"
                          >
                          </v-list-item-subtitle>
                        </v-list-item-content>
                      </v-list-item>

                      <v-card-actions>
                        <!-- author -->
                        <v-sheet
                          v-if="comment.author"
                          class="comment-btn"
                          width="60px"
                          style="position: absolute; right: 5px"
                          color="transparent"
                        >
                          <!--comment.id or p -->
                          <v-dialog
                            v-model="commentDialog[comment.id]"
                            width="600px"
                            :retain-focus="false"
                            v-if="!select"
                            dark
                            shaped
                          >
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn
                                class="justify-center"
                                color="blue"
                                dark
                                v-bind="attrs"
                                v-on="on"
                                small
                                icon
                                width="20"
                                @click.prevent="
                                  () => {
                                    Editname = comment.name;
                                    Editmessage = comment.message;
                                  }
                                "
                              >
                                <v-icon small>mdi-comment-edit</v-icon>
                              </v-btn>
                            </template>
                            <v-card style="border-radius: 25px">
                              <v-btn
                                depressed
                                color="error"
                                style="
                                  min-width: 20px;
                                  position: absolute;
                                  top: 0px;
                                  right: 0px;
                                "
                                @click="
                                  closeEditBtn(comment.id, currentTrackIndex, p)
                                "
                              >
                                x
                              </v-btn>
                              <v-form id="editForm" v-model="valid">
                                <v-card-text class="mt-5">
                                  <div class="reply">
                                    <input type="hidden" />
                                    <v-text-field
                                      tag="input"
                                      v-model.trim="Editname"
                                      label="Your name"
                                      :rules="rules"
                                      hide-details="auto"
                                      :validate-on-blur="true"
                                    ></v-text-field>
                                    <v-text-field
                                      tag="input"
                                      v-model.trim="Editmessage"
                                      label="Your comment"
                                      :rules="rules"
                                      hide-details="auto"
                                    ></v-text-field>
                                  </div>
                                </v-card-text>
                                <button
                                  :disabled="
                                    comment.name == '' ||
                                    comment.message == '' ||
                                    valid == false
                                  "
                                  type="submit"
                                  class="reply--button mb-5"
                                  @click.prevent="
                                    editComment(currentTrackIndex, p)
                                  "
                                >
                                  <i class="fa fa-paper-plane"></i> Send &nbsp;
                                </button>
                              </v-form>
                            </v-card>
                          </v-dialog>
                          <v-btn
                            class="justify-center"
                            color="red"
                            dark
                            small
                            icon
                            width="20"
                            @click.prevent="
                              () => {
                                snackbarDelete = true;
                                com = p;
                              }
                            "
                          >
                            <v-icon small>mdi-trash-can</v-icon>
                          </v-btn>
                          <v-snackbar
                            v-model="snackbarDelete"
                            :vertical="vertical"
                            shaped
                            centered
                            :timeout="-1"
                          >
                            Do You Want To Delete Your Comment

                            <template v-slot:action="{ attrs }">
                              <v-btn
                                color="pink"
                                text
                                v-bind="attrs"
                                @click="deleteComment(currentTrackIndex, com)"
                              >
                                Yes
                              </v-btn>
                              <v-btn
                                color="pink"
                                text
                                v-bind="attrs"
                                @click="snackbarDelete = false"
                              >
                                No
                              </v-btn>
                            </template>
                          </v-snackbar>
                        </v-sheet>
                      </v-card-actions>
                    </v-card>
                  </v-row>
                  <!-- add comment -->
                  <v-sheet>
                    <v-dialog
                      v-model="dialog[currentTrackIndex]"
                      width="600px"
                      heigh="fit-contain"
                      :retain-focus="false"
                      dark
                      shaped
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          class="justify-center mt-5"
                          color="success"
                          dark
                          outlined
                          rounded
                          v-bind="attrs"
                          v-on="on"
                        >
                          add your comment now
                          <v-icon small>mdi-comment-edit</v-icon>
                        </v-btn>
                      </template>
                      <v-card style="border-radius: 25px">
                        <v-btn
                          depressed
                          color="error"
                          style="
                            min-width: 20px;
                            position: absolute;
                            top: 0px;
                            right: 0px;
                          "
                          @click="closeDialog(currentTrackIndex)"
                        >
                          x
                        </v-btn>
                        <v-form id="form" ref="form1" v-model="valid">
                          <v-card-text class="mt-5">
                            <div class="reply">
                              <v-text-field
                                tag="input"
                                v-model.trim="name"
                                label="Your name"
                                :rules="rules"
                                hide-details="auto"
                                :validate-on-blur="true"
                              ></v-text-field>
                              <v-text-field
                                tag="input"
                                v-model.trim="message"
                                label="Your comment"
                                :rules="rules"
                                hide-details="auto"
                              ></v-text-field>
                            </div>
                          </v-card-text>
                          <button
                            :disabled="
                              name == '' || message == '' || valid == false
                            "
                            type="submit"
                            class="reply--button mb-5"
                            @click.prevent="submitComment(currentTrackIndex)"
                          >
                            <i class="fa fa-paper-plane"></i> Send &nbsp;
                          </button>
                        </v-form>
                      </v-card>
                    </v-dialog>
                  </v-sheet>
                </v-sheet>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>

        <!-- playlist -->
        <v-col :sm="12" :lg="6">
          <div class="wrapper" id="app">
            <div
              class="player"
              :style="{ backgroundColor: switch1 ? '' : color.color1 }"
            >
              <div class="player__top">
                <div class="player-cover">
                  <transition-group :name="transitionName">
                    <div v-for="(track, i) in tracks" :key="i">
                      <!-- <div
                        class="player-cover__item"
                        v-if="i === currentTrackIndex"
                        :style="{ backgroundImage: `url(${track.cover})` }"
                      ></div> -->

                      <!-- initial cover  -->
                      <div
                        class="player-cover__item"
                        v-if="i !== currentTrackIndex"
                        :style="{
                          backgroundImage: `url(${tracks[currentTrackIndex].cover})`,
                        }"
                      ></div>
                      <!-- initial cover  -->
                    </div>
                  </transition-group>
                </div>
                <div class="player-controls">
                  <div
                    class="player-controls__item -favorite"
                    :class="{ active: currentTrack.likes.bol }"
                    @click="favorite"
                  >
                    <svg class="icon">
                      <use xlink:href="#icon-heart-o"></use>
                    </svg>
                  </div>
                  <b
                    class="text-title"
                    :style="{ color: switch1 ? '' : color.color4 }"
                    >{{ currentTrack.likes.number }}</b
                  >
                    <!-- :href="currentTrack.url" -->
                  <a
                    :class="{ activeLink: currentTrack.share.bol }"
                    target="_blank"
                    class="player-controls__item -share"
                    @click="share(currentTrackIndex)"
                  >
                    <svg class="icon">
                      <use xlink:href="#icon-link"></use>
                    </svg>
                  </a>

                  <WebShare
                    :shareit="shareit"
                    @closeDialog="shareit = $event"
                    :Title="currentTrack.title"
                    :data="data"
                  />

                  <b
                    class="text-title"
                    :style="{ color: switch1 ? '' : color.color4 }"
                    >{{ currentTrack.share.number }}</b
                  >

                  <div class="player-controls__item" @click="prevTrack">
                    <svg class="icon">
                      <use xlink:href="#icon-prev"></use>
                    </svg>
                  </div>
                  <div class="player-controls__item" @click="nextTrack">
                    <svg class="icon">
                      <use xlink:href="#icon-next"></use>
                    </svg>
                  </div>
                  <div class="player-controls__item -xl js-play" @click="play">
                    <svg class="icon">
                      <use xlink:href="#icon-pause" v-if="isTimerPlaying"></use>
                      <use xlink:href="#icon-play" v-else></use>
                    </svg>
                  </div>
                </div>
              </div>
              <div class="progress" ref="progress">
                <div class="progress__top">
                  <div class="album-info" v-if="currentTrack">
                    <div
                      class="album-info__name"
                      :style="{ color: switch1 ? '' : color.color5 }"
                    >
                      {{ currentTrack.artist }}
                    </div>
                    <div
                      class="album-info__track"
                      :style="{ color: switch1 ? '' : color.color4 }"
                    >
                      {{ currentTrack.title }}
                    </div>
                  </div>
                  <div
                    class="progress__duration"
                    :style="{ color: switch1 ? '' : color.color4 }"
                  >
                    {{ duration }}
                  </div>
                </div>
                <div class="progress__bar" @click="clickProgress">
                  <div
                    class="progress__current"
                    :style="{ width: barWidth }"
                  ></div>
                </div>
                <div
                  class="progress__time"
                  :style="{ color: switch1 ? '' : color.color4 }"
                >
                  {{ currentTime }}
                </div>
              </div>
              <v-card class="mx-auto" max-width="400">
                <v-list
                  :dark="!switch1"
                  :style="{ backgroundColor: switch1 ? '' : color.color2 }"
                >
                  <v-list-group
                    sub-group
                    prepend-icon="mdi-playlist-music"
                    :color="switch1 ? color.color2 : 'green'"
                  >
                    <template v-slot:activator>
                      <v-list-item-content
                        :style="{
                          color: switch1 ? color.color1 : color.color5,
                        }"
                      >
                        <v-list-item-title>PlayList </v-list-item-title>
                      </v-list-item-content>
                    </template>

                    <v-divider inset></v-divider>

                    <v-list-item-group
                      v-model="model"
                      :color="switch1 ? color.color3 : 'green'"
                    >
                      <v-list-item
                        v-for="(track, i) in tracks"
                        :key="i"
                        @click="model = i"
                      >
                        <v-list-item-icon>
                          <v-icon
                            v-if="!isTimerPlaying && currentTrackIndex == i"
                            @click="play()"
                            >mdi-play</v-icon
                          >
                          <v-icon
                            v-if="isTimerPlaying && currentTrackIndex == i"
                            @click="play()"
                            >mdi-pause</v-icon
                          >
                        </v-list-item-icon>

                        <v-list-item-content>
                          <v-list-item-title
                            v-text="track.title"
                            @click="playlist(i)"
                          ></v-list-item-title>
                        </v-list-item-content>
                      </v-list-item>
                    </v-list-item-group>
                  </v-list-group>
                </v-list>
              </v-card>

              <div v-cloak></div>
            </div>
          </div>

          <svg
            xmlns="http://www.w3.org/2000/svg"
            hidden
            xmlns:xlink="http://www.w3.org/1999/xlink"
          >
            <defs>
              <symbol id="icon-heart-o" viewBox="0 0 32 32">
                <title>icon-heart-o</title>
                <path
                  d="M22.88 1.952c-2.72 0-5.184 1.28-6.88 3.456-1.696-2.176-4.16-3.456-6.88-3.456-4.48 0-9.024 3.648-9.024 10.592 0 7.232 7.776 12.704 15.072 17.248 0.256 0.16 0.544 0.256 0.832 0.256s0.576-0.096 0.832-0.256c7.296-4.544 15.072-10.016 15.072-17.248 0-6.944-4.544-10.592-9.024-10.592zM16 26.56c-4.864-3.072-12.736-8.288-12.736-14.016 0-5.088 3.040-7.424 5.824-7.424 2.368 0 4.384 1.504 5.408 4.032 0.256 0.608 0.832 0.992 1.472 0.992s1.248-0.384 1.472-0.992c1.024-2.528 3.040-4.032 5.408-4.032 2.816 0 5.824 2.304 5.824 7.424 0.064 5.728-7.808 10.976-12.672 14.016z"
                ></path>
                <path
                  d="M16 30.144c-0.32 0-0.64-0.096-0.896-0.256-7.296-4.576-15.104-10.048-15.104-17.344 0-7.008 4.576-10.688 9.12-10.688 2.656 0 5.152 1.216 6.88 3.392 1.728-2.144 4.224-3.392 6.88-3.392 4.544 0 9.12 3.68 9.12 10.688 0 7.296-7.808 12.768-15.104 17.344-0.256 0.16-0.576 0.256-0.896 0.256zM9.12 2.048c-4.448 0-8.928 3.616-8.928 10.496 0 7.168 7.744 12.64 15.008 17.152 0.48 0.288 1.12 0.288 1.568 0 7.264-4.544 15.008-9.984 15.008-17.152 0-6.88-4.48-10.496-8.928-10.496-2.656 0-5.088 1.216-6.816 3.392l-0.032 0.128-0.064-0.096c-1.696-2.176-4.192-3.424-6.816-3.424zM16 26.688l-0.064-0.032c-3.808-2.4-12.768-8.032-12.768-14.112 0-5.152 3.072-7.52 5.952-7.52 2.432 0 4.48 1.536 5.504 4.096 0.224 0.576 0.768 0.928 1.376 0.928s1.152-0.384 1.376-0.928c1.024-2.56 3.072-4.096 5.504-4.096 2.848 0 5.952 2.336 5.952 7.52 0 6.080-8.96 11.712-12.768 14.112l-0.064 0.032zM9.12 5.248c-2.752 0-5.728 2.304-5.728 7.328 0 5.952 8.8 11.488 12.608 13.92 3.808-2.4 12.608-7.968 12.608-13.92 0-5.024-2.976-7.328-5.728-7.328-2.336 0-4.32 1.472-5.312 3.968-0.256 0.64-0.864 1.056-1.568 1.056s-1.312-0.416-1.568-1.056c-0.992-2.496-2.976-3.968-5.312-3.968z"
                ></path>
                <path
                  d="M6.816 20.704c0.384 0.288 0.512 0.704 0.48 1.12 0.224 0.256 0.384 0.608 0.384 0.96 0 0.032 0 0.032 0 0.064 0.16 0.128 0.32 0.256 0.48 0.384 0.128 0.064 0.256 0.16 0.384 0.256 0.096 0.064 0.192 0.16 0.256 0.224 0.8 0.576 1.632 1.12 2.496 1.664 0.416 0.128 0.8 0.256 1.056 0.32 1.984 0.576 4.064 0.8 6.112 0.928 2.688-1.92 5.312-3.904 8-5.792 0.896-1.088 1.92-2.080 2.912-3.104v-7.552c-0.096-0.128-0.192-0.288-0.32-0.416-0.768-1.024-1.184-2.176-1.6-3.296-0.768-0.416-1.536-0.8-2.336-1.12-0.128-0.064-0.256-0.096-0.384-0.16h-21.568v12.992c1.312 0.672 2.496 1.6 3.648 2.528z"
                ></path>
              </symbol>
              <symbol id="icon-heart" viewBox="0 0 32 32">
                <title>icon-heart</title>
                <path
                  d="M22.88 1.952c-2.72 0-5.184 1.28-6.88 3.456-1.696-2.176-4.16-3.456-6.88-3.456-4.48 0-9.024 3.648-9.024 10.592 0 7.232 7.776 12.704 15.072 17.248 0.256 0.16 0.544 0.256 0.832 0.256s0.576-0.096 0.832-0.256c7.296-4.544 15.072-10.016 15.072-17.248 0-6.944-4.544-10.592-9.024-10.592zM16 26.56c-4.864-3.072-12.736-8.288-12.736-14.016 0-5.088 3.040-7.424 5.824-7.424 2.368 0 4.384 1.504 5.408 4.032 0.256 0.608 0.832 0.992 1.472 0.992s1.248-0.384 1.472-0.992c1.024-2.528 3.040-4.032 5.408-4.032 2.816 0 5.824 2.304 5.824 7.424 0.064 5.728-7.808 10.976-12.672 14.016z"
