              v-model="media"
              @input="setVolume()"
              @blur="volume = false"
            >
            </v-slider>
          </v-expand-transition>
          <v-icon large light @click="volume = !volume">mdi-volume-high</v-icon>
        </v-sheet>
      </v-row>
      <Message />
    </v-container>
  </v-app>
</template>

<script>
import { Carousel3d, Slide } from "vue-carousel-3d";
import search from "../components/subComment/search";
import WebShare from "../components/subComment/WebShare.vue";

export default {
  name: "my-work",
  data: () => ({
    // palylist
    audio: null,
    circleLeft: null,
    barWidth: null,
    duration: null,
    currentTime: null,
    isTimerPlaying: false,
    info: null,
    tracks: [
      {
        title: "Mekann Sahibi",
        artist: "Norm Ender",
        cover:
          "https://raw.githubusercontent.com/muhammederdem/mini-player/master/img/1.jpg",
        source:
          "https://raw.githubusercontent.com/muhammederdem/mini-player/master/mp3/1.mp3",
        url: "https://www.youtube.com/watch?v=z3wAjJXbYzA",
        likes: { bol: false, number: 35 },
        share: { bol: false, number: 35 },
        Label: "music",
        Released: "25/1/2021",
        Description:
          "It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, content here', making it look like readable English. Many desktop publishing packages and web page editors now use Lorem Ipsum as their default model text,",
        comments: [],
      },
    ],
    currentTrack: null,
    currentTrackIndex: 0,
    transitionName: null,

    // Card loading
    loading: false,
    selection: 1,
    rating: 4.5,
    model: 1,
    // search
    states: [],
    //comment
    DataComment: [],
    trackslength: 0,
    commentsIndex: 0,
    name: "",
    message: "",
    Editname: "",
    Editmessage: "",
    com: "",
    dialog: {},
    commentDialog: {},
    valid: true,
    ii: 1,
    resp: "",
    snackbar: false,
    snackbarDelete: false,
    vertical: true,
    rules: [
      (value) => !!value || "Required.",
      (value) => (value && value.length >= 3) || "Min 3 characters",
    ],
    // volume
    media: 100,
    volume: false,
    volumeBtn: false,
    //dark mode
    switch1: false,
    // shareit
    shareit: false,
    data: {},
  }),

  components: {
    Carousel3d,
    Slide,
    search,
    WebShare
  },

  methods: {
    play() {
      if (this.audio.paused) {
        this.audio.play();
        this.volumeBtn = true;
        this.isTimerPlaying = true;
      } else {
        this.audio.pause();
        this.isTimerPlaying = false;
        this.volumeBtn = false;
      }
    },
    setVolume() {
      this.audio.volume = this.media / 100;
      // console.log(this.audio.volume);
    },
    generateTime() {
      let width = (100 / this.audio.duration) * this.audio.currentTime;
      this.barWidth = width + "%";
      this.circleLeft = width + "%";
      let durmin = Math.floor(this.audio.duration / 60);
      let dursec = Math.floor(this.audio.duration - durmin * 60);
      let curmin = Math.floor(this.audio.currentTime / 60);
      let cursec = Math.floor(this.audio.currentTime - curmin * 60);
      if (durmin < 10) {
        durmin = "0" + durmin;
      }
      if (dursec < 10) {
        dursec = "0" + dursec;
      }
      if (curmin < 10) {
        curmin = "0" + curmin;
      }
      if (cursec < 10) {
        cursec = "0" + cursec;
      }
      this.duration = durmin + ":" + dursec;
      this.currentTime = curmin + ":" + cursec;
    },
    updateBar(x) {
      let progress = this.$refs.progress;
      let maxduration = this.audio.duration;
      let position = x - progress.offsetLeft;
      let percentage = (100 * position) / progress.offsetWidth;
      if (percentage > 100) {
        percentage = 100;
      }
      if (percentage < 0) {
        percentage = 0;
      }
      this.barWidth = percentage + "%";
      this.circleLeft = percentage + "%";
      this.audio.currentTime = (maxduration * percentage) / 100;
      this.audio.play();
    },
    clickProgress(e) {
      this.isTimerPlaying = true;
      this.audio.pause();
      this.updateBar(e.pageX);
    },
    prevTrack() {
      this.transitionName = "scale-in";
      this.isShowCover = false;
      if (this.currentTrackIndex > 0) {
        this.currentTrackIndex--;
      } else {
        this.currentTrackIndex = this.tracks.length - 1;
      }
      this.currentTrack = this.tracks[this.currentTrackIndex];
      this.resetPlayer();
      this.$refs.mycarousel.goSlide(this.currentTrackIndex);

      this.$router.replace({
        name: "my-work",
        params: { id: this.currentTrackIndex },
      });
    },
    nextTrack() {
      this.transitionName = "scale-out";
      this.isShowCover = false;
      if (this.currentTrackIndex < this.tracks.length - 1) {
        this.currentTrackIndex++;
      } else {
        this.currentTrackIndex = 0;
      }
      this.currentTrack = this.tracks[this.currentTrackIndex];
      this.resetPlayer();
      this.$refs.mycarousel.goSlide(this.currentTrackIndex);

      this.$router.replace({
        name: "my-work",
        params: { id: this.currentTrackIndex },
      });
    },
    resetPlayer() {
      this.barWidth = 0;
      this.circleLeft = 0;
      this.audio.currentTime = 0;
      this.audio.src = this.currentTrack.source;
      setTimeout(() => {
        if (this.isTimerPlaying) {
          this.audio.play();
        } else {
          this.audio.pause();
        }
      }, 300);
    },
    favorite() {
      this.tracks[this.currentTrackIndex].likes.bol =
        !this.tracks[this.currentTrackIndex].likes.bol;
      var i = this.currentTrackIndex;
      if (this.tracks[i].likes.bol == true) {
        this.tracks[i].likes.number++;
        // ADD TO BACKEND
        const axios = require("axios");
        let base_url =
          //  `http://192.168.1.10/music%20project/music%20project/public/api/Music/addLike/${this.tracks[i].id}`;
          `http://asmusicbackend-07251.herokuapp.com/public/api/Music/addLike/${this.tracks[i].id}`;
        axios.get(base_url);
      } else {
        this.tracks[i].likes.number--;
        const axios = require("axios");
        let base_url =
          //  `http://192.168.1.10/music%20project/music%20project/public/api/Music/subLike/${this.tracks[i].id}`;
          `http://asmusicbackend-07251.herokuapp.com/public/api/Music/subLike/${this.tracks[i].id}`;
        axios.get(base_url);
      }
      localStorage.setItem(
        `MusicLikesOf${i}`,
        JSON.stringify(this.tracks[i].likes)
      );
    },
    share(i) {
      // for mobilr
      if (this.$vuetify.breakpoint.mobile) {
        this.shareitAction(
          "share a track ",
          `listen to ${this.tracks[i].title}`,
          `https://abanoubsamirnaguib.github.io/ArtistAbanoubSamir${this.$router.currentRoute.fullPath}`
        );
      }
      // for web
      else {
        this.data = {
          url: `https://abanoubsamirnaguib.github.io/ArtistAbanoubSamir${this.$router.currentRoute.fullPath}`,
          title: this.tracks[i].title,
          description: this.tracks[i].Description,
          quote: this.tracks[i].artist,
        };
        this.shareit = true;
      }
      // this.tracks[i].share.bol = true;
      // if (this.tracks[i].share.bol == true) {
      //   this.tracks[i].share.number++;
      //   const axios = require("axios");
      //   let base_url =
      //     //  `http://192.168.1.10/music%20project/music%20project/public/api/Music/addShare/${this.tracks[i].id}`;
      //     `http://asmusicbackend-07251.herokuapp.com/public/api/Music/addShare/${this.tracks[i].id}`;
      //   axios.get(base_url);
      // }
      // localStorage.setItem(
      //   `MusicShareOf${i}`,
      //   JSON.stringify(this.tracks[i].share)
      // );
    },
    playlist(i) {
      this.transitionName = "scale-in";
      this.isShowCover = false;
      this.currentTrackIndex = i;
      this.currentTrack = this.tracks[this.currentTrackIndex];
      this.$router.replace({ name: "my-work", params: { id: i } });
      this.$refs.mycarousel.goSlide(i);
      this.resetPlayer();
      this.reserve();
    },
    onAfterSlideChange(i) {
      this.info = this.tracks[i];
      // console.log(i);
      this.commentsIndex = i;
    },
    //card loading
    reserve() {
      this.loading = true;
      setTimeout(() => (this.loading = false), 1000);
    },
    // search
    GetSearchIndex(e) {
      this.playlist(e);
    },
    resetSelect(select) {
      this.select = select;
    },
    // components
    getComets() {
      const axios = require("axios");
      var base_url =
        // "http://192.168.1.10/music%20project/music%20project/public/api/comments/Music";
        "http://asmusicbackend-07251.herokuapp.com/public/api/comments/Music";
      axios.get(base_url).then((responseComment) => {
        var DataComment = responseComment.data.data;
        this.DataComment = DataComment;
        // console.log(this.DataComment);
        for (let n = 0; n <= this.trackslength; n++) {
          if (window.localStorage.getItem(`MusicCommentsOf${n}`) !== null) {
            var Comments = this.DataComment.filter((e) => {
              return e.Number == n;
            });
            var x = 0;
            for (let i = 0; i < Comments.length; i++) {
              if (
                JSON.parse(localStorage.getItem(`MusicCommentsOf${n}`))[x] &&
                Comments[i].id ==
                  JSON.parse(localStorage.getItem(`MusicCommentsOf${n}`))[x].id
              ) {
                // console.log(JSON.parse((localStorage.getItem(`MusicCommentsOf${n}`)))[x].id + " "+ i );
                Comments[i].author =
                  // true;
                  JSON.parse(localStorage.getItem(`MusicCommentsOf${n}`))[
                    x
                  ].author;
                x++;
              }
            }
          }
        }
      });
      this.tracks[this.currentTrackIndex].comments = this.comments;
    },
    submitComment(n) {
      this.comments.push({
        // id: newComment.id,
        name: this.name,
        message: this.message,
        Number: n,
        color: "red",
        author: true,
      });
      //api
      const axios = require("axios");
      let base_url =
        // `http://192.168.1.10/music%20project/music%20project/public/api/comments/addMusicComment`;
        `http://asmusicbackend-07251.herokuapp.com/public/api/comments/addMusicComment`;
      axios
        .post(base_url, {
          name: this.name,
          message: this.message,
          Number: n,
          color: "green",
        })
        .then((response) => {
          let Data = response.data;
          this.resp = Data.Success;
          this.snackbar = true;
          var newComment = Data.data;
          // console.log(newComment);
          this.comments[this.comments.length - 1] = {
            id: newComment.id,
            name: newComment.name,
            message: newComment.message,
            Number: n,
            color: "red",
            author: true,
          };

          var authorCommnets = this.comments.filter((e) => {
            return e.author == true;
          });
          localStorage.setItem(
            `MusicCommentsOf${n}`,
            JSON.stringify(authorCommnets)
          );
        });

      this.name = "";
      this.message = "";

      this.Editname = "";
      this.Editmessage = "";
      // this.dialog[n] = false;
      if (this.$refs.form1.length > 0) {
        this.$refs.form1.map((el) => {
          el.reset();
        });
      } else this.$refs.form1.reset();
      this.dialog[n] = false;
    },

    editComment(n, p) {
      this.comments[p] = {
        id: this.comments[p].id,
        name: this.Editname,
        message: this.Editmessage,
        Number: n,
        color: this.comments[p].color,
        author: true,
      };
      //api
      const axios = require("axios");
      let base_url =
        //  `http://192.168.1.10/music%20project/music%20project/public/api/comments/editMusicComment/${this.comments[p].id}`;
        `http://asmusicbackend-07251.herokuapp.com/public/api/comments/editMusicComment/${this.comments[p].id}`;
      axios
        .post(base_url, {
          name: this.Editname,
          message: this.Editmessage,
          Number: n,
          color: "green",
        })
        .then((response) => {
          let Data = response.data;
          this.resp = Data.Success;
          this.snackbar = true;

          var newComment = Data.data;
          this.comments[p] = {
            id: newComment.id,
            name: newComment.name,
            message: newComment.message,
            Number: n,
            color: this.comments[p].color,
            author: true,
          };
          // console.log(this.tracks[n].comments[p]);
          var authorCommnets = this.comments.filter((e) => {
            return e.author == true;
          });

          localStorage.setItem(
            `MusicCommentsOf${n}`,
            JSON.stringify(authorCommnets)
          );
        });
      // console.log(this.tracks[n].comments[p]);
      this.Editname = "";
      this.Editmessage = "";

      this.commentDialog[this.comments[p].id] = false;
    },
    deleteComment(n, p) {
      // api
      console.log(n);
      const axios = require("axios");
      let base_url =
        //  `http://192.168.1.10/music%20project/music%20project/public/api/comments/deleteMusicComment/${this.comments[p].id}`;
        `http://asmusicbackend-07251.herokuapp.com/public/api/comments/deleteMusicComment/${this.comments[p].id}`;
      axios.post(base_url).then((response) => {
        this.resp = response.data.Success;
        this.snackbar = true;
        // console.log(Data);
        var authorCommnets = this.comments.filter((e) => {
          return e.author == true;
        });

        localStorage.setItem(
          `MusicCommentsOf${n}`,
          JSON.stringify(authorCommnets)
        );
      });

      this.comments.splice(p, 1);
      this.snackbarDelete = false;
    },
    closeEditBtn(id) {
      this.commentDialog[id] = false;
    },
    closeDialog(n) {
      // console.log(this.$refs.form1);
      this.dialog[n] = false;
      if (this.$refs.form1.length > 0) {
        this.$refs.form1.map((el) => {
          el.reset();
        });
      } else this.$refs.form1.reset();

      this.name = "";
      this.message = "";

      this.Editname = "";
      this.Editmessage = "";
    },
    colorDark() {
      this.switch1 = this.$store.state.switch;
      if (this.switch1 == true) {
        this.$store.commit("color");
      }
      if (this.switch1 == false) {
        this.$store.commit("dark");
      }
    },
  },

  beforeCreate() {
    // api
    const axios = require("axios");
    var base_url =
      // "http://192.168.1.10/music%20project/music%20project/public/api/Music";
      "http://asmusicbackend-07251.herokuapp.com/public/api/Music";
    axios
      .get(base_url)
      .then((response) => {
        var Data = response.data.data;
        this.tracks = Data;

        if (
          this.$route.params.id > 0 &&
          this.$route.params.id <= this.tracks.length - 1
        ) {
          this.currentTrackIndex = this.$route.params.id;
          this.currentTrack = this.tracks[this.currentTrackIndex];
        } else {
          this.currentTrack = this.tracks[0];
        }

        // this.currentTrackIndex = 0;
        // this.currentTrack = this.tracks[this.currentTrackIndex];
        this.resetPlayer();
        // console.log(this.tracks);
        // get likes&share&comments from localStorage
        for (let n = 0; n < this.tracks.length; n++) {
          if (localStorage.getItem(`MusicLikesOf${n}`) !== null) {
            this.tracks[n].likes.bol = JSON.parse(
              localStorage.getItem(`MusicLikesOf${n}`)
            ).bol;
          }
          if (localStorage.getItem(`MusicShareOf${n}`) !== null) {
            this.tracks[n].share.bol = JSON.parse(
              localStorage.getItem(`MusicShareOf${n}`)
            ).bol;
          }
        }
        this.trackslength = this.tracks.length;
        //info
        this.info = this.tracks[this.currentTrackIndex];
        // search
        for (let n = 0; n < this.tracks.length; n++) {
          this.states[n] = this.tracks[n].title;
          //comments
        }
      })
      .catch((error) => {
        console.log(error);
        this.errored = true;
      });
  },
  created() {
    let vm = this;
    this.currentTrack = this.tracks[this.currentTrackIndex];
    // this.currentTrack = this.tracks[0];
    this.audio = new Audio();
    this.audio.src = this.currentTrack.source;
    this.audio.ontimeupdate = function () {
      vm.generateTime();
    };
    this.audio.onloadedmetadata = function () {
      vm.generateTime();
    };
    this.audio.onended = function () {
      vm.nextTrack();
      this.isTimerPlaying = true;
    };

    // this is optional (for preload covers)
    for (let index = 0; index < this.tracks.length; index++) {
      const element = this.tracks[index];
      let link = document.createElement("link");
      link.rel = "prefetch";
      link.href = element.cover;
      link.as = "image";
      document.head.appendChild(link);
    }
    //info
    // this.info = this.tracks[this.currentTrackIndex];
    this.getComets();
    // darkmode
    this.colorDark();
  },
  computed: {
    comments() {
      var data = this.DataComment.filter((e) => {
        return e.Number == this.commentsIndex;
      });
      return data;
    },
    color() {
      return this.$store.state.color;
    },
  },
  watch: {
    tracks() {
      this.trackslength = this.tracks.length;
      this.getComets();
      // console.log(this.trackslength);
    },
    color() {
      this.switch1 = this.$store.state.switch;
    },
  },
  destroyed() {
    this.audio.pause();
  },
};
</script>
<style lang="scss" scoped>
#inspire {
  min-height: 100%;
  background: url("../assets/img/m2.jpg") rgba(0, 0, 0, 0.4);
